import empty from '@/components/empty.vue';
import axios from '@/utils/uniAxios.js'
export default {
	components: {
		empty
	},
	data() {
		return {
			src: '../static/shopcarempty.png',
			msg: '购物车没有商品，你还可以',
			list: [],
			update: '编辑',
			checked: true,
			stepValue: 1,
			num: 1,
			deleShow: false,
			list: [],
			checkeds: false,
			total:0,
			allNum:0,//总的数量
		}
	},
	onLoad(option) {
		this.getShopCar();
	},
	methods: {
		// 促销信息
		promote(n) {
			var str = n.split('：');
			return str;
		},
		// 获取某个商品的规格
		changeAttr(id) {
			axios.post('/cart/getProduct/' + id).then(res => {
				// 弹出规格的弹窗
			})
		},
		// 修改优惠
		editPrefe() {

		},
		// 获取购物车列表
		getShopCar() {
			axios.post('/cart/list/promotion').then(res => {
				if (res.data.code == 200) {
					console.log(res)
					this.list = res.data.data
				}
			})
		},
		// 点击编辑或完成
		edit() {
			if (this.update == '编辑') {
				this.update = '完成';
				this.deleShow = true
			} else {
				this.update = '编辑';
				this.deleShow = false
			}
		},
		// 最终的全选
		allSlect(e) {
			this.checkeds = e.detail;
			if(this.checkeds){//全部选择
				this.total = 0;
				let totalPrices = '';
				this.list.forEach((el,index)=>{
					el.deleteStatus = 1;
					totalPrices=el.price*el.quantity;
					this.total += totalPrices;
				})
				this.allNum = this.list.length;
			}else{//全不选中
				this.total = 0;
				this.list.forEach((el,index)=>{
					el.deleteStatus = 0;
				});
				this.allNum = 0;
			}
			
		},
		//每个商店的全选
		onChangeAll() {

		},
		// 删除某个商品
		delete(id) {
			let obj = {
				id: id
			}
			axios.post('/cart/delete', id).then(res => {
				if (res.data.code == 200) {
					console.log(res)
				}
			})
		},
		// 单个商品的选择
		singleOnChange(status, index) {
			if(status==1){//表示取消选中
				this.list[index].deleteStatus = 0;
				let price = this.list[index].price*this.list[index].quantity;
				this.total-= price;
				this.allNum--;
			}else{
				this.list[index].deleteStatus = 1;
				let price = this.list[index].price*this.list[index].quantity;
				this.total+= price;
				this.$store.commit('totalMoney', price);
				this.allNum++
			}
			
		},
		// 减少商品数量
		// 同时判断总价
		minus(item, index, id, realStock) {
			console.log(item)
			if (item.quantity == 1) {
				item.quantity = 1;
			} else {
				item.quantity--;
				realStock++;
				if(item.deleteStatus==1){//表示选中
					this.total-=item.price*1
				}
			}
			let obj = {
				id: id,
				quantity: item.quantity
			}
			axios.post('/cart/update/quantity', obj).then(res => {

			})
		},
		// 增加商品数量
		add(item, index, id, realStock) {
			item.quantity++;
			realStock--;
			if(item.deleteStatus==1){//表示选中
				this.total+=item.price*1
			}
			// 请求接口
			let obj = {
				id: id,
				quantity: item.quantity
			}
			axios.post('/cart/update/quantity', obj).then(res => {

			})
		}
	}
}
